// eslint-disable-next-line camelcase
import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import { throttle } from 'lodash';

import BaseModal from '../../../../shared/components/modal';
import fetchComment, { COMMENT_PAGE_SIZE } from './fetchComment';
import commentCardTemplate from './commentCardTemplate';
import { Loading } from '../../../../shared/components/toast';
import helper from '../../../../shared/utils/helper';

const COMMENT_FETCH_STATUS = {
  PENDING: 'PENDING',
  SUCCESS: 'SUCCESS',
  ERROR: 'ERROR',
};

const $commentMoreContainer = $('#MpModalproduct-comment-more .product-comment-mobile-more .container');

let commentMobileCurrentPage = 0;
let commentFetchStatus = null;

const appendCommentItem = (data) => {
  const $loading = $('#MpModalproduct-comment-more .product-comment-mobile-more-loading');
  const lastChildrenIndex = $commentMoreContainer.children().length - 2 < 0 ? 0 : $commentMoreContainer.children().length - 2;
  const cards = data.list.map((d, index) => {
    const { info, images } = commentCardTemplate(d);
    return $('<div></div>')
      .addClass(['product-comment-card', 'custom-body-color'])
      .attr({ 'data-index': lastChildrenIndex + index + (lastChildrenIndex === 0 ? 0 : 1), 'data-more': true }) // loading+1
      .append([info, images]);
  });
  $commentMoreContainer.append([...cards, $loading]);
  window.lozadObserver.observe();
  if (data.total <= commentMobileCurrentPage * COMMENT_PAGE_SIZE) {
    $loading.remove();
  }
};

const fetchCommentHandle = async (pageNum) => {
  commentFetchStatus = COMMENT_FETCH_STATUS.PENDING;
  try {
    const response = await fetchComment(pageNum);
    commentMobileCurrentPage += 1;
    if (response && response.data) {
      const { data } = response;
      const commentMore = SL_State.rootState.commentMore ? SL_State.rootState.commentMore : { list: [] };
      SL_State.set('commentMore', { ...data, list: [...commentMore.list, ...data.list] });
      appendCommentItem(data);
    }
    commentFetchStatus = COMMENT_FETCH_STATUS.SUCCESS;
  } catch (error) {
    commentFetchStatus = COMMENT_FETCH_STATUS.ERROR;
    console.log(error);
  }
};

const scrollEvent = () => {
  const comments = SL_State.rootState.comments ?? { list: [] };
  const scrollFn = throttle(async function (e) {
    const { scrollTop, scrollHeight, clientHeight } = e.currentTarget;
    if (commentFetchStatus === COMMENT_FETCH_STATUS.PENDING) {
      return;
    }
    if (scrollTop + clientHeight > scrollHeight - 300 && comments.total > commentMobileCurrentPage * 6) {
      fetchCommentHandle(commentMobileCurrentPage + 1);
    }
  }, 300);
  $('#MpModalproduct-comment-more .mp-modal__body').on('scroll', scrollFn);
};

const openCommentMoreModal = () => {
  const modal = new BaseModal({
    modalId: 'product-comment-more',
  });
  modal.init();
  const loading = new Loading({
    target: '#MpModalproduct-comment-more .product-comment-mobile-more-loading',
    duration: -1,
  });
  $('.product-comment-mobile-summary-more').on('click', function () {
    loading.open();
    modal.show();
    scrollEvent();
    fetchCommentHandle(commentMobileCurrentPage + 1);
  });
  $('.product-comment-mobile-more-header-back').on('click', function () {
    modal.hide();
    commentMobileCurrentPage = 0;
    $commentMoreContainer.empty().append($('<div></div>').addClass('product-comment-mobile-more-loading'));
  });
  helper.listenPlatform(() => {
    if (helper.getPlatform() === 'pc') {
      modal.hide();
    }
  });
};

openCommentMoreModal();
