import './style/index.scss';
import '../../shared/pre-bundle';

import './js/comment/open-comment-more-modal';
import './js/comment/open-comment-detail-modal';


import './js/comment/comment-pagination';
import '../commons/js/product-item';
import './js/report';

import initPreview from './js/product-preview';

/*  */
import '@yy/sl-theme-shared/components/hbs/comment/js/index.js';

const sectionId = $('[data-product-preview-section-id]').data('product-preview-section-id');
const sectionRecentlyProductId = $('[data-recently-product-section-id]').data('recently-product-section-id');

let unload = initPreview({ id: 'productDetail', statePath: 'product', offsetTop: 20 });

$(document).on('shopline:section:load', (e) => {
  if (e.detail.sectionId === sectionId) {
    unload = initPreview({ id: 'productDetail', statePath: 'product', offsetTop: 20 });
  } else if (e.detail.sectionId === sectionRecentlyProductId) {
    window.lozadObserver.observe();
  }
});

$(document).on('shopline:section:unload', (e) => {
  if (e.detail.sectionId === sectionId) {
    unload?.destroy?.();
    unload = null;
  }
});

// 
$('.products-detail-info .product-info .comments').on('click', () => {
  document.querySelector('#product-comment-container').scrollIntoView();
});
