import dayjs from 'dayjs';

import getRateSnippet from '../../../commons/js/get-rate-snippet';
import nameDesensitization from '../../../commons/js/nameDesensitization';

const commentCardTemplate = (data) => {
  const { nickname, imageList, content, time, score } = data;
  const info = $('<div></div>')
    .addClass('product-comment-card-info')
    .append([
      $('<div></div>').addClass('product-comment-card-info-nickname').append(nameDesensitization(nickname)),
      $('<div></div>').addClass('product-comment-card-info-time').append(dayjs(time).format('MMM DD,YYYY')),
      $('<div></div>')
        .addClass('product-comment-card-info-rate')
        .append(getRateSnippet({ value: score })),
      $('<div></div>').addClass('product-comment-card-info-content').append(content),
    ]);
  const images = $('<div></div>')
    .addClass('product-comment-card-image')
    .append((imageList || []).map((image) => $('<img></img>').attr({ 'data-src': image }).addClass('lozad')));
  if (imageList && imageList.length > 1) {
    images.append(
      $('<div></div>')
        .addClass('product-comment-card-image-num')
        .html(
          `<svg width="10" height="11" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill="#fff" d="M1.25 5.023h7.5v1.25h-7.5z"></path>
            <path fill="#fff" d="M4.375 9.398v-7.5h1.25v7.5z"></path>
          </svg>${imageList && imageList.length ? imageList.length - 1 : 0}`,
        ),
    );
  }

  return {
    info,
    images,
  };
};

export default commentCardTemplate;
