// eslint-disable-next-line camelcase
import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import request from '@yy/sl-theme-shared/utils/request';

export const COMMENT_PAGE_SIZE = 6;

const fetchComment = (pageNum) => {
  const { spuSeq } = SL_State.rootState.product.spu;
  return request.post('/product/detail/comment/query/list', {
    pageNum,
    pageSize: COMMENT_PAGE_SIZE,
    spuSeq,
  });
};

export default fetchComment;
