// eslint-disable-next-line camelcase
import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import fetchComment from './fetchComment';
import commentCardTemplate from './commentCardTemplate';
import { Loading } from '../../../../shared/components/toast';
import pagination from '../../../commons/js/pagination';

let commentPcCurrentPage = 1;

const replaceCommentItem = (data) => {
  const cards = data.list.map((d, index) => {
    const { info, images } = commentCardTemplate(d);
    return $('<div></div>')
      .addClass(['product-comment-card', 'custom-body-color'])
      .attr({ 'data-index': index })
      .append(d.imageList && d.imageList.length ? [info, images] : [info]);
  });
  const firstColChild = cards.filter((_, index) => index % 2 === 0);
  const secondColChild = cards.filter((_, index) => index % 2 !== 0);

  $('.product-comment-pc-wrap-col').eq(0).empty().append(firstColChild);
  $('.product-comment-pc-wrap-col').eq(1).empty().append(secondColChild);
  window.lozadObserver.observe();
};

const onCommentPagination = () => {
  const total = SL_State.rootState.comments ? SL_State.rootState.comments.total : 0;
  pagination.init({
    $el: $('#comment_pagination'),
    current: 1,
    pageSize: 6,
    total,
    isLoadRender: false,
    onChange: async (page) => {
      const loading = new Loading({
        target: '.product-comment-pc',
        duration: -1,
      });
      if (page !== commentPcCurrentPage) {
        try {
          loading.open();
          const response = await fetchComment(page);
          loading.close();
          commentPcCurrentPage = page;
          SL_State.set('comments', response.data);
          if (response && response.data) {
            const { data } = response;
            replaceCommentItem(data);
          }
        } catch (error) {
          loading.close();
          console.error(error);
        }
      }
    },
  });
};

onCommentPagination();
